:root {
  --dark: #192734;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.dark-theme{
  background-color: #192734;
  color: aliceblue;
  --color1: aliceblue;
}

.light-theme{
  background-color: white;
  color: #192734;
  --color1: #192734;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
  font-weight: 600;
  font-size: 4rem;

}

h2{
  font-weight: 600;
  font-size: 2.5rem;

}

h3{
  font-weight: 300;
  font-size: 1.1rem;

}

h4{
  font-weight: 600;
  font-size: 1.2rem;
}

ul{
  padding: 0;
}

.icon{
  vertical-align: middle;
}

button{
  background-color: white;
  border-color: #192734;
  border-width: 3px;
  border-radius: 15px;
  padding: 5px 20px;
  font-size: 16px;
  color:#192734;
  cursor: pointer;
}

li, a{
  text-decoration: none;
}


button:hover{
  background-color: #192734;
  color: white;
  text-shadow: 0 0 .65px white, 0 0 .65px white;
  transition: 200ms ease-in-out;
}

.app-list .icon{
  padding-right: 10px;
}




@media (width <= 900px) {
  h1{
    font-size: 3rem;
  }

  h2{
    font-size: 1.75rem;
  }

  h3{
    font-size: 1rem;
  }

  h4{
    font-size: 1rem;
  }
  
}
